// ...........footer.............

$bg-footer: $primary;
$bg-copyright: $gray-300;
$bg-social: $primary-dark;

.footer-three {
    padding-top: 45px;
    background-color:$bg-footer;
    color: color-yiq($bg-footer);
    .logo {
        font-family: 'Pasajero';
        color: color-yiq($bg-footer);
        font-size: 34px;
        display: flex;
        align-items: center;
        line-height: normal;
        text-decoration: none;
        strong {
            color: $secondary;
        }
    }
    hr {
        margin-top: 5px;
        margin-bottom: 1rem;
        border: 0;
        border-top: $border-width solid $border-color;
        &:before {
            height: 1px;
            background-color: $secondary;
            content: "";
            position: absolute;
            width: 85px;
            margin-top: -1px;
        }
    }
    h5 {
        font-size: $font-size-lg;
        text-transform: uppercase;
        margin: 0;
    }  
    ul{
        li{
            a{
                .fa{
                    padding-right: 5px;
                }
                &:hover{
                    color: $secondary;
                }
            }
        }
    }        
    .contact-list  {
        li {
            display: flex;
            align-items: center;
            list-style: none;
            font-size: $font-size-base;
            margin-bottom: 15px;
            .fas {
                display: flex;
                align-items: center;
                justify-content: start;
                font-size: $font-size-lg;
                color: $secondary;
                height: 30px;
                width: 30px;
            }
            a {
                color: color-yiq($bg-footer);
            }
        }
    }
    .links-list{
        li{
            list-style-type: none;
            margin-bottom: 8px;
            a{
                display: block;
                font-size: $font-size-base;
                color: color-yiq($bg-footer);
                &:hover {
                    color: $secondary;
                }
            }
        }
    }
            
   
    .payment-mthod{
        margin-bottom: 30px;
        .footer-image {
            .img-fluid{
                height: 20px;
            }
        }
    }

      // -----------------social
      .social-content{
        background-color:$bg-social;
        color: color-yiq($bg-social);
        padding-top: 15px;
        padding-bottom: 15px;
        margin-top: 45px;
        .socials {
            display: block;
            .list {
                display: block;
                padding-left: 0;
                margin-bottom: 0;
                li{
                    display: inline-block;
                    a {
                        background-color: $primary;
                        color: color-yiq($bg-footer);
                        font-size: $font-size-lg;
                        width: 30px;
                        height: 30px;
                        display: flex;
                        justify-content: center;
                        align-items: center;
                        text-decoration: none;
                        border-radius: $border-radius;
                        border: $border-width solid $border-color;
    
                        &:hover
                            {
                            background-color: $secondary;
                            color: color-yiq($secondary);
                            border-color: $secondary;
                        }
                    }
                }
            }   
        }
        .newsletter {
            
            .block {
                display: flex;  
                position: relative;
                align-items: center;
                
                h5{
                    margin: 0;
                    font-size: 1.7rem;
                    
                }
                .form-inline {
                    display: flex;
                    flex-flow: row wrap;
                    align-items: center;
                    width: 330px;
                    margin-left: auto;
                    .search {
                        background-color: $white;
                        border-radius: $border-radius;
            
                        position: relative;
                        width: 100%;
                        
                        .select-control{
                            float: left;
                            position: relative;
                            border-right: $border-width solid $border-color;
                            width: 159px;
                            height: 40px;
            
                            .form-control{
                                height: 40px;
                                width: 100%;
                            }
                            &::before{
                                font-family:"Font Awesome 5 Free";
                                font-weight: 900; /* regular style/weight */
                                content: "\f0d7";
                                position: absolute;
                                top: 10px;
                                right: 15px;
                                color: $body-color;
                            }
                        }
                        input {
                            float: right;
                            height: 40px;
                            padding: 8px 12px 8px 12px;
                            width: 100%;
                            border-radius: 0;
                            border: none;
                            outline: none !important;
                            border: $border-width solid $border-color;
                        }
                        .btn-secondary {
                            position: absolute;
                            right: 1px;
                            top: 1px;
                            bottom: 1px;
                            height: calc(100% - 2px);
                            width: 102px;
                        }          
                    }
                } 
            }
        }
        
    }  
    .copyright-content{
        background-color:$bg-copyright;
        color: color-yiq($bg-copyright);
        padding-top: 10px;
        padding-bottom: 10px;
    
        
    
        .footer-info{
            text-align: right;
            a{
                color:color-yiq($bg-copyright);
            }
            p{
                color:color-yiq($bg-copyright);
                margin: 0;
                padding: 0;
            }
        }
    }    
}

